/* @import url("https://fonts.cdnfonts.com/css/gotham-9"); */

html {
  font-size: 10px;
}

body {
  margin: 0;
  font-family: "poppins", "roboto", "roboto-condensed", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(245, 247, 249);
  color: "#EDDFD0";
  font-size: 1.4rem;
}

@font-face {
  font-family: "Gotham-Bold";
  src: url(./assets/fonts/gotham/Gotham-Bold.otf);
}
@font-face {
  font-family: "Gotham-Light";
  src: url(./assets/fonts/gotham/Gotham-Light.otf);
}
@font-face {
  font-family: "HelveticaNeueMedium";
  src: url(./assets/fonts/helvetica-neue-5/HelveticaNeueMedium.otf);
}

@font-face {
  font-family: "HelveticaNeueLight";
  src: url(./assets/fonts/helvetica-neue-5/HelveticaNeueLight.otf);
}

/* perfect scrollbar visible  */
.ps .ps__rail-x {
  opacity: 0.6;
}

.MainBg,
body {
  background-image: url(./assets/images/mainbg.jpg);
  height: 100%;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
.footer-bg {
  background-image: url(./assets/images/footerbg.jpg);
  height: 100%;
  background-size: cover;
  background-position: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@keyframes Float {
  from {
    transform: translateX(100%) translateZ(0);
  }
  to {
    transform: translateX(-15%) translateZ(0);
  }
}

@keyframes FadeFloat {
  0%,
  100% {
    opacity: 0;
  }
  5%,
  90% {
    opacity: 1;
  }
}

.cloud-small {
  background-image: url(./assets/images/small-cloud.png);
}
.cloud-big {
  background-image: url(./assets/images/big-cloud.png);
}

#Clouds {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 30%;
  overflow: hidden;
  animation: FadeIn 3.1s ease-out;
  user-select: none;
}

.cloud,
.cloud-small,
.cloud-big {
  position: fixed;
  width: 100%;
  max-height: 300px;
  height: 65%;
  background-repeat: no-repeat;
  background-size: auto 100%;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  animation-name: Float, FadeFloat;
  z-index: 9999;
}

.cloud:nth-child(2) {
  top: 20%;
}

.cloud.Foreground:nth-child(2) {
  animation-duration: 64;
  height: 50;
}

.cloud.Background:nth-child(2) {
  animation-duration: 82.6923076923;
  height: 44.5454545455;
}

.cloud:nth-child(1) {
  animation-delay: 4s;
  top: 33%;
}

.cloud.Foreground:nth-child(1) {
  animation-duration: 68;
  height: 52.5;
}

.cloud.Background:nth-child(1) {
  animation-duration: 93.0769230769;
  height: 46.8181818182;
}
